import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { validateAll } from 'indicative/validator';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { compose } from 'redux';
import withAuthRedirect from '../../hoc/withAuthRedirect';
import css from '../LoginPage/LoginPage.module.css';
import * as sessionOperations from '../../redux/session/sessionOperations';
import { getIsAuthenticated } from '../../redux/session/sessionSelectors';

const validationRules = {
  name: 'required|min:3|max:50',
  email: 'required|email',
  password: 'required|min:6|max:30',
  confirmPassword: 'required|min:6|max:30|same:password',
};

const validationMessages = {
  required: field => `${field} ' `,
  'name.min': "'     3 ",
  'name.max': "'     50 ",
  'email.email': '   ',
  'password.min': '     6 ',
  'password.max': '     30 ',
  'confirmPassword.min': '     6 ',
  'confirmPassword.max': '     30 ',
  'confirmPassword.same': '  ',
};

class RegistrationPage extends Component {
  state = {
    name: '',
    email: '',
    password: '',
    confirmPassword: '',
    error: null,
  };

  handleChange = e => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handleSubmit = e => {
    const { name, email, password, confirmPassword } = this.state;
    const { onRegistrate } = this.props;
    e.preventDefault();
    validateAll(
      { name, email, password, confirmPassword },
      validationRules,
      validationMessages,
    )
      .then(data => {
        const credentials = {
          email: data.email,
          password: data.password,
          name: { fullName: data.name },
        };
        onRegistrate(credentials);
        this.setState({
          name: '',
          email: '',
          password: '',
          confirmPassword: '',
          error: null,
        });
      })
      .catch(errors => {
        const formatedErrors = {};
        errors.forEach(error => {
          formatedErrors[error.field] = error.message;
        });
        this.setState({
          error: formatedErrors,
        });
      });
  };

  render() {
    const { name, email, password, confirmPassword, error } = this.state;
    return (
      <section className={css.reg_section}>
        <div className={css.reg_form}>
          <div className={css.custom_container}>
            <form onSubmit={this.handleSubmit}>
              <div className={css.reg_social}>
                <a
                  className={css.reg_social_button}
                  href="https://book-read.goit.co.ua/api/v1/auth/google"
                >
                  Google
                </a>
              </div>
              <label htmlFor="name">
                <span>&apos;</span>
                <input
                  onChange={this.handleChange}
                  type="text"
                  id="name"
                  name="name"
                  value={name}
                  placeholder="'"
                />
                {error && <span className={css.error}>{error.name}</span>}
              </label>
              <label htmlFor="email">
                <span> </span>
                <input
                  onChange={this.handleChange}
                  type="email"
                  id="email"
                  name="email"
                  value={email}
                  placeholder="your@mail.com"
                />
                {error && <span className={css.error}>{error.email}</span>}
              </label>
              <label htmlFor="password">
                <span></span>
                <input
                  onChange={this.handleChange}
                  autoComplete="off"
                  type="password"
                  id="password"
                  name="password"
                  value={password}
                  placeholder=""
                />
                {error && <span className={css.error}>{error.password}</span>}
              </label>
              <label htmlFor="confirmPassword">
                <span> </span>
                <input
                  onChange={this.handleChange}
                  type="password"
                  id="confirmPassword"
                  name="confirmPassword"
                  value={confirmPassword}
                  placeholder=" "
                />
                {error && (
                  <span className={css.error}>{error.confirmPassword}</span>
                )}
              </label>
              <button type="submit"></button>
              <p className={css.relink}>
                  ? <Link to="/login"></Link>
              </p>
            </form>
          </div>
        </div>
        <div className={css.reg_text}>
          <div className={css.custom_container}>
            <div className={css.reg_text_content}>
              <h1>Books Reading</h1>
              <h2> </h2>
              <ul>
                <li>      </li>
                <li>     </li>
                <li>    </li>
              </ul>
              <h2>  </h2>
              <ul>
                <li>     </li>
                <li>
                         
                </li>
                <li>  </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
    );
  }
}
RegistrationPage.propTypes = {
  onRegistrate: PropTypes.func.isRequired,
  history: PropTypes.shape({
    replace: PropTypes.func,
  }).isRequired,
};

const mapStateToProps = state => ({
  isAuthenticated: getIsAuthenticated(state),
});

const mapDispatchToProps = {
  onRegistrate: sessionOperations.registrateOperation,
};

//  
// export default withAuthRedirect(
//   connect(
//     mapStateToProps,
//     mapDispatchToProps,
//   )(LoginPage),
// );

//  
export default compose(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  ),
  withAuthRedirect,
)(RegistrationPage);
